.contacts {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 24px;
    justify-content: center;
    background-color: rgb(191, 191, 191);
    margin: 0;
    padding: 0 20px 20px 20px;
    width: 100%;
    box-sizing: border-box;
}

.contacts__title {
    margin: 0;
    padding: 0;
    margin-top: -90px;
    font-size: calc(1.65em + 1vw);
    text-align: center;
}

.contacts__logo {
    grid-row: 4/5;
    margin: 0 auto;
    padding: 0;
}

.contacts__logo-img {
    margin: 0;
    padding: 0;
    width: 200px;

}

.contacts__list {
    margin: 0;
    padding: 0;
    @include list-reset;
    text-align: center;
}

.contacts__item {
    margin-bottom: 10px;
}

.contacts__city {
    margin: 0;
    padding: 0;
    margin-bottom: 5px;
    font-size: calc(1em + 0.8vw);
    color: #ffffff;
}

.contacts__tel {
    text-decoration: none;
    font-size: calc(1em + 0.8vw);
    color: #ffffff;

    &:hover,
    :focus,
    :active {
        color: rgb(52, 59, 66);
    }
}

.contacts__wrap {
    display: flex;
    justify-content: center;
    line-height: 0;
}

.contacts__map-wrap {
    position: relative;
    height: fit-content;
}

.contacts__map {
    display: block;
    margin: 0 auto;
    padding: 0;
}

.contacts__marker {
    position: absolute;
    top: 74%;
    left: 47%;
    width: calc(.5em + 0.5vw);
    height: calc(1em + 3.5vh);
    background-size: contain;
    background-image: url("../img/marker.png");
    background-repeat: no-repeat;
    background-position: 0 0;
}

@media (min-width: $tablet-width) {
    .contacts {
        position: relative;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 0 .4fr 3fr;
        grid-column-gap: 40px;
        padding-left: 50px;
        padding-right: 50px;
        width: auto;
    }

    .contacts__title {
        grid-column: 1/3;
    }

    .contacts__logo {
        grid-row: 2/3;
        margin: 0;
    }

    .contacts__list {
        grid-column: 1/2;
        text-align: start;

    }

    .contacts__item {
        margin-bottom: 32px;
    }

    .contacts__wrap {
        position: relative;
        top: -27%;
        right: 0;
        grid-row: 2/4;
    }

}

@media (min-width: $desktop-width) {
    .contacts {
        grid-row-gap: 53px;
    }

    .contacts__item {
        margin-bottom: 64px;
    }

    .contacts__logo {
        margin-bottom: 9px;
    }

    .contacts__logo-img {
        width: 418px;
        height: auto;
    }

    .contacts__city {
        margin-bottom: 18px;
    }
}