.features {
    display: grid;
    margin: 0;
    padding: 0;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-row-gap: 20px;
    margin-left: 20px;
    margin-right: 20px;
}

.features__title {
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: calc(1.65em + 1vw);
}

.features__text {
    margin: 0;
    padding: 0;
    font-size: 18px;
    text-align: center;
    margin-bottom: 15px;
}

@media (min-width: $desktop-width) {
    .features {
        margin-left: 50px;
        margin-right: 50px;
        grid-row-gap: 59px;
    }

    .features__text {
        text-align: start;
        font-size: 24px;
        margin-bottom: 40px;
    }
}