.header {
  display: grid;
  grid-row-gap: 20px;
  justify-items: center;
  margin: 0;
  padding: 0;
  background-color: rgb(17, 28, 45);
  align-content: center;
}

.header__wrap {
  padding: 20px 20px 0 20px;
  background-size: cover;
  background-image: url(../img/banner.jpg);
  background-repeat: no-repeat;
  background-position: 80% center;
  align-content: center;
}

.header__nav {
  display: grid;
  grid-row-gap: 24px;
  justify-items: center;
}

.header__logo {
  margin: 0;
  padding: 0;
  align-self: center;
}

.header__logo-img {
  width: calc(200px + 10vw);
}

.header__contacts {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}

.header__tel {
  font-size: calc(12px + 1vw);
  text-decoration: none;
  color: #ffffff;

  &:hover,
  :focus,
  :active {
    color: rgb(172, 180, 197);
  }
}

.main-nav {
  @include list-reset;
  text-align: center;
  margin-bottom: 24px;
}

.main-nav__btn {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #ffffff;
  display: block;
  width: 280px;
  padding-top: 18px;
  padding-bottom: 18px;
  border: 3px solid rgb(0, 255, 0);
  border-radius: 10px;
  font-size: 20px;
  box-sizing: border-box;

  &:hover,
  :focus,
  :active {
    color: rgb(172, 180, 197);
  }
}

.title {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 24px;
  letter-spacing: 1px;
}

@media (min-width: $tablet-width) {
  .header__wrap {
    background-image: url("../img/banner.jpg");
  }

}

@media (min-width: $desktop-width) {
  .header {
    width: 100%;
    grid-row-gap: 116px;
    background-size: cover;
    background-image: url(../img/banner.jpg);
    background-repeat: no-repeat;
    background-position: 90% center;
    align-content: center;
  }

  .header__wrap {
    width: 100%;
    box-sizing: border-box;
    background-image: none;
    padding: 50px 50px 0 50px;
  }

  .header__nav {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: 1fr;
    grid-column-gap: 20px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 264px;
  }

  .main-nav {
    margin-bottom: 0;
    margin-left: auto;
  }

  .header__contacts {
    margin-left: auto;
  }

  .main-nav__btn {
    font-size: 24px;
    width: 341px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .title {
    font-size: 36px;
    max-width: 1120px;
    text-align: start;
  }
}