.use {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 20px;
  margin: 0;
  justify-content: center;
}

.use__title {
  margin: 0;
  padding: 0;
  font-size: calc(1.65em + 1vw);
  text-align: center;
  margin-left: 20px;
  margin-right: 20px;
}

.use__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 20px;
  justify-content: center;
  margin: 0;
  padding: 0;
  @include list-reset;
}

.use__item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 15px;
}

.use__item-title {
  margin: 0;
  padding: 0;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 24px;
  text-align: center;
}

.use__text {
  position: relative;
  margin: 0;
  padding: 0;
  padding: calc(5em + 2vw) 20px 10px 20px;
  font-size: 20px;
  text-align: center;
  background-color: rgb(116, 123, 136);

  &::before {
    content: "";
    position: absolute;
    width: 100px;
    height: 100px;
    top: 5%;
    left: 50%;
    transform: translate(-50%, 0);
    background-repeat: no-repeat;
    background-size: 100% auto;
  }
}

.use__text--school::before {
  width: 90px;
  background-image: url("../img/school.svg");
  top: 8%;
}

.use__text--plant::before {
  background-image: url("../img/plant.svg");
  top: 10%;
}

.use__text--city-hall::before {
  background-image: url("../img/city-hall.svg");
}


@media (min-width: $desktop-width) {
  .use {
    grid-template-columns: 1fr;
    grid-row-gap: 80px;
    margin: 0;
    margin-left: 50px;
    margin-right: 50px;
  }

  .use__list {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 62px;
    margin: 0;
  }

  .use__item {
    align-content: start;
    grid-template-rows: 1fr 6fr;
    grid-row-gap: 32px;
  }

  .use__text {
    font-size: 24px;
  }
}