.page-main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, auto);
  justify-items: center;
  margin: 0;
  grid-row-gap: 40px;
  background: #ffffff url("../img/background.jpg") repeat;
  background-size: 100% auto;
  padding-top: 40px;
}

@media (min-width: $desktop-width) {
  .page-main {
    padding-top: 158px;
    grid-row-gap: 80px;
  }
}