@font-face {
  font-family: "Century Gothic";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/CenturyGothic.woff2") format("woff2"), url("../fonts/CenturyGothic.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  font-size: 16px;
  font-family: "Century Gothic", "Arial", sans-serif;
  color: #000000;
  font-style: normal;
  background-color: #ffffff;
}

@media (min-width: 660px) {
  body {
    min-width: 660px;
  }
}
@media (min-width: 960px) {
  body {
    width: 100%;
  }
}
img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  display: grid;
  grid-row-gap: 20px;
  justify-items: center;
  margin: 0;
  padding: 0;
  background-color: #111c2d;
  align-content: center;
}

.header__wrap {
  padding: 20px 20px 0 20px;
  background-size: cover;
  background-image: url(../img/banner.jpg);
  background-repeat: no-repeat;
  background-position: 80% center;
  align-content: center;
}

.header__nav {
  display: grid;
  grid-row-gap: 24px;
  justify-items: center;
}

.header__logo {
  margin: 0;
  padding: 0;
  align-self: center;
}

.header__logo-img {
  width: calc(200px + 10vw);
}

.header__contacts {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}

.header__tel {
  font-size: calc(12px + 1vw);
  text-decoration: none;
  color: #ffffff;
}
.header__tel:hover,
.header__tel :focus,
.header__tel :active {
  color: #acb4c5;
}

.main-nav {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  margin-bottom: 24px;
}

.main-nav__btn {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #ffffff;
  display: block;
  width: 280px;
  padding-top: 18px;
  padding-bottom: 18px;
  border: 3px solid lime;
  border-radius: 10px;
  font-size: 20px;
  box-sizing: border-box;
}
.main-nav__btn:hover,
.main-nav__btn :focus,
.main-nav__btn :active {
  color: #acb4c5;
}

.title {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 24px;
  letter-spacing: 1px;
}

@media (min-width: 660px) {
  .header__wrap {
    background-image: url("../img/banner.jpg");
  }
}
@media (min-width: 960px) {
  .header {
    width: 100%;
    grid-row-gap: 116px;
    background-size: cover;
    background-image: url(../img/banner.jpg);
    background-repeat: no-repeat;
    background-position: 90% center;
    align-content: center;
  }

  .header__wrap {
    width: 100%;
    box-sizing: border-box;
    background-image: none;
    padding: 50px 50px 0 50px;
  }

  .header__nav {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: 1fr;
    grid-column-gap: 20px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 264px;
  }

  .main-nav {
    margin-bottom: 0;
    margin-left: auto;
  }

  .header__contacts {
    margin-left: auto;
  }

  .main-nav__btn {
    font-size: 24px;
    width: 341px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .title {
    font-size: 36px;
    max-width: 1120px;
    text-align: start;
  }
}
.page-main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, auto);
  justify-items: center;
  margin: 0;
  grid-row-gap: 40px;
  background: #ffffff url("../img/background.jpg") repeat;
  background-size: 100% auto;
  padding-top: 40px;
}

@media (min-width: 960px) {
  .page-main {
    padding-top: 158px;
    grid-row-gap: 80px;
  }
}
.use {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 20px;
  margin: 0;
  justify-content: center;
}

.use__title {
  margin: 0;
  padding: 0;
  font-size: calc(1.65em + 1vw);
  text-align: center;
  margin-left: 20px;
  margin-right: 20px;
}

.use__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 20px;
  justify-content: center;
  margin: 0;
  padding: 0;
  margin: 0;
  padding: 0;
  list-style: none;
}

.use__item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 15px;
}

.use__item-title {
  margin: 0;
  padding: 0;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 24px;
  text-align: center;
}

.use__text {
  position: relative;
  margin: 0;
  padding: 0;
  padding: calc(5em + 2vw) 20px 10px 20px;
  font-size: 20px;
  text-align: center;
  background-color: #747b88;
}
.use__text::before {
  content: "";
  position: absolute;
  width: 100px;
  height: 100px;
  top: 5%;
  left: 50%;
  transform: translate(-50%, 0);
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.use__text--school::before {
  width: 90px;
  background-image: url("../img/school.svg");
  top: 8%;
}

.use__text--plant::before {
  background-image: url("../img/plant.svg");
  top: 10%;
}

.use__text--city-hall::before {
  background-image: url("../img/city-hall.svg");
}

@media (min-width: 960px) {
  .use {
    grid-template-columns: 1fr;
    grid-row-gap: 80px;
    margin: 0;
    margin-left: 50px;
    margin-right: 50px;
  }

  .use__list {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 62px;
    margin: 0;
  }

  .use__item {
    align-content: start;
    grid-template-rows: 1fr 6fr;
    grid-row-gap: 32px;
  }

  .use__text {
    font-size: 24px;
  }
}
.features {
  display: grid;
  margin: 0;
  padding: 0;
  justify-content: center;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.features__title {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: calc(1.65em + 1vw);
}

.features__text {
  margin: 0;
  padding: 0;
  font-size: 18px;
  text-align: center;
  margin-bottom: 15px;
}

@media (min-width: 960px) {
  .features {
    margin-left: 50px;
    margin-right: 50px;
    grid-row-gap: 59px;
  }

  .features__text {
    text-align: start;
    font-size: 24px;
    margin-bottom: 40px;
  }
}
.info {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto 65px;
  grid-row-gap: 35px;
  justify-content: center;
  width: 100%;
}

.info__title {
  margin: 0;
  padding: 0;
  font-size: calc(1.65em + 1vw);
  text-align: center;
  margin-left: 20px;
  margin-right: 20px;
}

.info__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, auto);
  grid-row-gap: 10px;
  margin: 0;
  padding: 0;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.info__item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 15px;
  position: relative;
  padding: 170px 20px 15px 20px;
}
.info__item:nth-child(odd) {
  background-color: rgba(0, 10, 30, 0.45);
}
.info__item::before {
  content: "";
  position: absolute;
  top: 7%;
  left: 50%;
  height: 140px;
  transform: translate(-50%, 0);
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.info__item--tablet::before {
  width: 80px;
  background-image: url("../img/tablet.png");
}

.info__item--screen::before {
  width: 180px;
  background-image: url("../img/screen.png");
}

.info__item--door::before {
  width: 140px;
  background-image: url("../img/door.png");
}

.info__item--list-clock::before {
  width: 125px;
  background-image: url("../img/list-clock.png");
}

.info__item-title {
  margin: 0;
  padding: 0;
  font-size: 24px;
  text-align: center;
}

.info__text {
  margin: 0;
  padding: 0;
  font-size: 20px;
  text-align: center;
}

@media (min-width: 960px) {
  .info {
    grid-row-gap: 74px;
    margin-left: 50px;
    margin-right: 50px;
  }

  .info__list {
    grid-template-rows: repeat(4, minmax(330px, 1fr));
  }

  .info__item {
    min-height: 360px;
    padding: 40px 20px 20px calc(535px + 1vw);
    align-content: flex-start;
    grid-row-gap: 48px;
  }
  .info__item::before {
    left: 17em;
  }

  .info__item--tablet::before {
    width: 179px;
    height: 311px;
  }

  .info__item--screen::before {
    width: 446px;
    height: 322px;
  }

  .info__item--door::before {
    width: 343px;
    height: 315px;
  }

  .info__item--list-clock::before {
    width: 300px;
    height: 300px;
  }

  .info__item-title {
    text-align: start;
    font-size: 36px;
  }

  .info__text {
    text-align: start;
    font-size: 24px;
  }
}
.contacts {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 24px;
  justify-content: center;
  background-color: #bfbfbf;
  margin: 0;
  padding: 0 20px 20px 20px;
  width: 100%;
  box-sizing: border-box;
}

.contacts__title {
  margin: 0;
  padding: 0;
  margin-top: -90px;
  font-size: calc(1.65em + 1vw);
  text-align: center;
}

.contacts__logo {
  grid-row: 4/5;
  margin: 0 auto;
  padding: 0;
}

.contacts__logo-img {
  margin: 0;
  padding: 0;
  width: 200px;
}

.contacts__list {
  margin: 0;
  padding: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}

.contacts__item {
  margin-bottom: 10px;
}

.contacts__city {
  margin: 0;
  padding: 0;
  margin-bottom: 5px;
  font-size: calc(1em + 0.8vw);
  color: #ffffff;
}

.contacts__tel {
  text-decoration: none;
  font-size: calc(1em + 0.8vw);
  color: #ffffff;
}
.contacts__tel:hover,
.contacts__tel :focus,
.contacts__tel :active {
  color: #343b42;
}

.contacts__wrap {
  display: flex;
  justify-content: center;
  line-height: 0;
}

.contacts__map-wrap {
  position: relative;
  height: fit-content;
}

.contacts__map {
  display: block;
  margin: 0 auto;
  padding: 0;
}

.contacts__marker {
  position: absolute;
  top: 74%;
  left: 47%;
  width: calc(.5em + 0.5vw);
  height: calc(1em + 3.5vh);
  background-size: contain;
  background-image: url("../img/marker.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}

@media (min-width: 660px) {
  .contacts {
    position: relative;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 0 0.4fr 3fr;
    grid-column-gap: 40px;
    padding-left: 50px;
    padding-right: 50px;
    width: auto;
  }

  .contacts__title {
    grid-column: 1/3;
  }

  .contacts__logo {
    grid-row: 2/3;
    margin: 0;
  }

  .contacts__list {
    grid-column: 1/2;
    text-align: start;
  }

  .contacts__item {
    margin-bottom: 32px;
  }

  .contacts__wrap {
    position: relative;
    top: -27%;
    right: 0;
    grid-row: 2/4;
  }
}
@media (min-width: 960px) {
  .contacts {
    grid-row-gap: 53px;
  }

  .contacts__item {
    margin-bottom: 64px;
  }

  .contacts__logo {
    margin-bottom: 9px;
  }

  .contacts__logo-img {
    width: 418px;
    height: auto;
  }

  .contacts__city {
    margin-bottom: 18px;
  }
}
.clients {
  background-color: #111c2d;
}

.clients__list {
  display: grid;
  grid-row-gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  justify-content: center;
  margin: 0 20px 20px 20px;
}

.clients__item {
  color: #ffffff;
  font-size: calc(1em + 1vw);
  border: 3px solid #ffffff;
  border-radius: 5px;
  text-align: center;
  padding: 5px 5px 8px 5px;
}

@media (min-width: 960px) {
  .clients {
    display: grid;
    background-color: transparent;
    margin: 0 50px 0 50px;
  }

  .clients__list {
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 96px;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 124px;
  }
}