.info {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto 65px;
    grid-row-gap: 35px;
    justify-content: center;
    width: 100%;
}

.info__title {
    margin: 0;
    padding: 0;
    font-size: calc(1.65em + 1vw);
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;
}

.info__list {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    grid-row-gap: 10px;
    margin: 0;
    padding: 0;
    justify-content: center;
    @include list-reset;
}

.info__item {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-row-gap: 15px;
    position: relative;
    padding: 170px 20px 15px 20px;

    &:nth-child(odd) {
        background-color: rgba(0, 10, 30, 0.45);
    }

    &::before {
        content: "";
        position: absolute;
        top: 7%;
        left: 50%;
        height: 140px;
        transform: translate(-50%, 0);
        background-repeat: no-repeat;
        background-size: 100% auto;
    }
}

.info__item--tablet::before {
    width: 80px;
    background-image: url("../img/tablet.png");
}

.info__item--screen::before {
    width: 180px;
    background-image: url("../img/screen.png");
}

.info__item--door::before {
    width: 140px;
    background-image: url("../img/door.png");
}

.info__item--list-clock::before {
    width: 125px;
    background-image: url("../img/list-clock.png");
}

.info__item-title {
    margin: 0;
    padding: 0;
    font-size: 24px;
    text-align: center;
}

.info__text {
    margin: 0;
    padding: 0;
    font-size: 20px;
    text-align: center;
}

@media (min-width: $desktop-width) {
    .info {
        grid-row-gap: 74px;
        margin-left: 50px;
        margin-right: 50px;
    }

    .info__list {
        grid-template-rows: repeat(4, minmax(330px, 1fr));
    }

    .info__item {
        min-height: 360px;
        padding: 40px 20px 20px calc(535px + 1vw);
        align-content: flex-start;
        grid-row-gap: 48px;

        &::before {

            left: 17em;
        }
    }

    .info__item--tablet::before {
        width: 179px;
        height: 311px;
    }

    .info__item--screen::before {
        width: 446px;
        height: 322px;

    }

    .info__item--door::before {
        width: 343px;
        height: 315px;

    }

    .info__item--list-clock::before {
        width: 300px;
        height: 300px;
    }

    .info__item-title {
        text-align: start;
        font-size: 36px;
    }

    .info__text {
        text-align: start;
        font-size: 24px;
    }
}